.BotPenguin-chat * {
  box-sizing: content-box !important;
  -webkit-box-sizing: content-box !important;
}
@media screen and (max-width: 550px) {
  #BotPenguin-messenger {
    width: 100% !important;
    height: 100% !important;
    bottom: 0 !important;
    right: 0 !important;
    left: 0 !important;
    border-radius: 0 !important;
    margin: 0 !important;
  }
}
#BotPenguin-messenger {
  background: #fff;
  z-index: 2147483647;
  width: 360px;
  height: 500px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 3px 1px hsla(0, 0%, 57.3%, 0.3);
  left: auto;
  display: none;
}
.left {
  left: 20px !important;
}
.center {
  left: 0 !important;
  right: 0 !important;
  margin: auto !important;
}
#botpenguin-launcher-12 {
  position: fixed;
  z-index: 2147483647;
  bottom: 20px;
  right: 20px;
  cursor: pointer;
  visibility: hidden;
  transition: visibility 0.7s, opacity 0.5s linear;
  opacity: 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.botpenguin-launcher-text-12 {
  font-weight: 600;
  color: #000;
  position: relative;
  margin-bottom: 8px;
  max-width: 230px;
  background: #fff;
  padding: 13px;
  vertical-align: bottom;
  cursor: pointer;
  border-radius: 20px 20px 3px 20px;
  margin-right: 8px;
  margin-top: 0;
  font-size: 16px;
  word-spacing: 2px;
}
.botpenguin-launcher-text-12,
span.botpenguin-launcher-image-12 {
  box-shadow: 0 0 3px 1px hsla(0, 0%, 57.3%, 0.3);
  overflow: hidden;
  display: inline-block;
}
span.botpenguin-launcher-image-12 {
  vertical-align: middle;
  border-radius: 50%;
  height: 44px;
  width: 44px;
  padding: 2px;
}
span.botpenguin-launcher-image-12 img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.botpenguin-launcher-text-12:after {
  display: block;
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  right: -10px;
  bottom: 10px;
  background-color: inherit;
  transform: translateX(-6px) rotate(45deg);
  pointer-events: none;
  box-shadow: 2px -2px 4px 0 rgba(0, 0, 0, 0.2);
}
.botpenguin-right {
  right: 20px;
  left: auto;
}
.botpenguin-left {
  right: auto;
  left: 20px;
}
.botpenguin-center {
  right: 20px !important;
  top: auto !important;
  bottom: 50% !important;
}
@media screen and (max-width: 550px) {
  .botpenguin-launcher-text-12 {
    max-width: 215px;
    padding: 12px;
    font-size: 16px;
  }
  span.botpenguin-launcher-image-12 {
    height: 44px;
    width: 44px;
  }
}
@media screen and (max-width: 400px) {
  .botpenguin-launcher-text-12 {
    max-width: 185px;
    padding: 12px;
    font-size: 16px;
  }
  span.botpenguin-launcher-image-12 {
    height: 44px;
    width: 44px;
  }
}
@media screen and (max-width: 300px) {
  .botpenguin-launcher-text-12 {
    max-width: 160px;
    padding: 12px;
    font-size: 16px;
  }
  span.botpenguin-launcher-image-12 {
    height: 44px;
    width: 44px;
  }
}
.hide {
  -webkit-animation: right-animate 0.6s ease forwards;
  animation: right-animate 0.6s ease forwards;
}
@-webkit-keyframes right-animate {
  0% {
    transform: translateX(0) scaleX(1) scaleY(1);
  }
  to {
    transform: translateX(65%) scaleX(0);
  }
}
@keyframes right-animate {
  0% {
    transform: translateX(0) scaleX(1) scaleY(1);
  }
  to {
    transform: translateX(65%) scaleX(0);
  }
}


#hubspot-messages-iframe-container{
  display: none !important;
}
