@tailwind base;
@tailwind components;
@tailwind utilities;



@font-face {
  font-family: 'bot-custom-iran-sans';
  src: url('https://hamyar.chat/cdn/widget/v1.0.0/sourceWidget/public/iranyekanwebregular.woff2') format('woff2'),
  /* Add additional font formats if necessary */
  /* Specify the correct path to the font files */
}

:root {
  --bot-theme-primary-background-color: #ffffff;
  --bot-theme-secondary-background-color: #f8fafc;
  --bot-theme-text-color: #000;
  --bot-position-left: 0;
  --bot-position-right: 0;
  --bot-position-top: 0;
  --bot-position-bottom: 0;
}

.default-primary-theme{
      background: var(--bot-theme-primary-background-color);
          color: var(--bot-theme-text-color)
}

.default-primary-theme-force{
      background: var(--bot-theme-primary-background-color) !important;
          color: var(--bot-theme-text-color) !important;
}

.default-secondary-theme{
      background: var(--bot-theme-secondary-background-color);
          color: var(--bot-theme-text-color)
}

.position-for-widget{
    left: var(--bot-position-left) !important;
    right: var(--bot-position-right) !important;
    top: var(--bot-position-top) !important;
    bottom: var(--bot-position-bottom) !important;
}



#HAMYARBOT{
  margin: 0;
  font-family: "iran-sans", sans-serif;
}

#HAMYARBOT h1,
#HAMYARBOT h2,
#HAMYARBOT h3,
#HAMYARBOT h4,
#HAMYARBOT p,
#HAMYARBOT span,
#HAMYARBOT li,
#HAMYARBOT em,
#HAMYARBOT strong,
#HAMYARBOT blockquote,
#HAMYARBOT article,
#HAMYARBOT figure,
#HAMYARBOT figcaption,
#HAMYARBOT label,
#HAMYARBOT a {
  font-family: bot-custom-iran-sans;
}

#HAMYARBOT h1,#HAMYARBOT h2, #HAMYARBOT h4 {
  font-family: bot-custom-iran-sans;
  font-size: 39px;
}
#HAMYARBOT h3 {
  font-family: bot-custom-iran-sans;
}
#HAMYARBOT .italic-super {
  font-family: Matter-RegularItalic;
}
#HAMYARBOT p {
  font-size: .875rem;
  font-family: bot-custom-iran-sans;
}
#HAMYARBOT span {
  font-size: .875rem;
  font-family: bot-custom-iran-sans;
}

#HAMYARBOT a {
  text-decoration: underline;
  color: blue; /* Change this to your desired shade of blue */
}

#HAMYARBOT @m
@media (max-width: 1200px) {
  #HAMYARBOT h1,
  #HAMYARBOT h2 {
    font-size: 31px;
  }
  #HAMYARBOT p {
    font-size: 15px;
  }
}
#HAMYARBOT @m
@media (max-width: 930px) {
  #HAMYARBOT h1,
  #HAMYARBOT h2 {
    font-size: 31px;
  }
  #HAMYARBOT p {
    font-size: 16px;
  }
}
#HAMYARBOT @m
@media (max-width: 500px) {
  #HAMYARBOT h1,
  #HAMYARBOT h2 {
    font-size: 30px;
  }
  #HAMYARBOT p {
    font-size: 16px;
  }
}
