:root{
--mybot-css-variable-name-for-scroll:#118cfc99;
}
.main_ui_container{
        /* Dont clear this double values they are for browser support handling */
        background-image: url("https://hamyar.chat/cdn/widget/v1.0.0/sourceWidget/public/chat-bg.svg");
        background-size: 300px 300px;
        height: 100vh; /* Fallback for browsers that don't support dvh */
        height: 100dvh; /* Use dvh for supported browsers */
        width: 100vw;
        width: 100dvw;
        display: flex;
        flex-direction: column;
        /*background: #f1f5f8;*/
        z-index: 100000000000000;
        box-shadow: 0 5px 40px rgba(0, 0, 0, .16);
        border-radius: 0px;
        position : fixed;
        top: 0;

}



.custom_scrollbar::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}

.custom_scrollbar::-webkit-scrollbar-thumb {
  background: var(--mybot-css-variable-name-for-scroll);
  opacity: 0.3;
  border-radius: 20px;
}

.custom_scrollbar::-webkit-scrollbar-thumb:hover {
  background: var(--mybot-css-variable-name-for-scroll);
}
.wrapper_style {
  background: #fff;
  z-index: 2147483647;
  width: 360px;
  height: 500px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 3px 1px hsla(0, 0%, 57.3%, 0.3);
  left: auto;
  display: none;
}

.action_btn p{
    direction: rtl;
    font-size: 0.8rem;
    font-family: iran-sans !important;
    font-weight: 400;
    line-height: 1.5;
}

.message_text p{
font-family: iran-sans !important;

}


.markdown-styles p {
margin-top: 0px;
margin-bottom: 0px;
}
@media screen and (max-width: 550px) {
  .markdown-styles p {
    font-size: 14px;
  }
}